@import '../helpers/_helpers';

.title {}

.box2 {
	margin: 0 0 0 50px;
	overflow: auto;
	border: 5px solid rgba(0, 0, 0, 0.4);
	width: 200px;
}


.position {
	width: 160px;
	height: 100px;
	cursor: pointer;

	&:before {
		position: absolute;
		content: 'frontend-developer html-верстальщик web-developer billiard player billiard coach';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #f4f4f4;
		padding: 17px;
		z-index: 10;
		opacity: 0;
		visibility: hidden;
		transition: all 0.3s ease;
	}
	&:hover:before {
		opacity: 1;
		visibility: visible;
	}
}
